<template>
  <section class="content-wrapper">
    <h2>messageBox</h2>
    <p>
      
    </p>

    <div class="demo-tip">
      <p>
        MessageBox
        
        Dialog
      </p>
    </div>

    <demo-block
      title=""
      :code-height="600"
      introduction=""
    >
      <template v-slot:code-des>
        <p>
           <code>this.$msgbox()</code> <code>alert</code> 
          true <code>alert</code>
          <code>Promise</code>
        </p>
      </template>
      <template v-slot:demo>
        <jj-button type="text" @click="handleClick"
          > Message Box</jj-button
        >
      </template>
      <template v-slot:code>
        <code class="html">{{ $tools.formatCode(vueCode) }}</code>
      </template>
    </demo-block>

    <demo-block
      title=""
      :code-height="900"
      introduction=""
    >
      <template v-slot:code-des>
        <p>
           <code>confirm</code> Message Box
          <code>options</code><code>type</code><code>success</code><code>error</code><code>info</code><code>warning</code>
        </p>
      </template>
      <template v-slot:demo>
        <jj-button type="text" @click="handleClickWarning"
          > type  warning  Message Box</jj-button
        >

        <jj-button type="text" @click="handleClickSuccess"
          > type success  Message Box</jj-button
        >
      </template>
      <template v-slot:code>
        <code class="html">{{ $tools.formatCode(vueCode2) }}</code>
      </template>
    </demo-block>

    <h3></h3>
    <p> this.$msgbox(options) </p>

    <h3></h3>
    <div class="demo-code-block">
      <p>import { MessageBox } from 'element-ui'</p>
      <p>MessageBox(options)</p>
    </div>

    <h3>Options</h3>
    <demo-table :data="optionsDescription"></demo-table>
  </section>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      optionsDescription: [
        {
          parameter: 'title',
          description: 'MessageBox ',
          dataTypes: 'String',
          optional: '',
          default: '',
        },
        {
          parameter: 'content',
          description: 'MessageBox ',
          dataTypes: 'String',
          optional: '',
          default: '',
        },
        {
          parameter: 'confirmBtnText',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: '',
        },
        {
          parameter: 'cancelBtnText',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: '',
        },

        {
          parameter: 'type',
          description: '',
          dataTypes: 'String',
          optional: 'success / info / warning / error',
          default: '',
        },
        {
          parameter: 'iconClass',
          description: ' type',
          dataTypes: 'String',
          optional: '',
          default: '',
        },
        {
          parameter: 'callback',
          description:
            ' Promise MessageBox ',
          dataTypes: 'Function',
          optional: '',
          default: '',
        },

        {
          parameter: 'showClose',
          description: 'MessageBox ',
          dataTypes: 'Boolean',
          optional: '',
          default: 'true',
        },
      ],
      vueCode: `
          <template>
            <jj-button type="text" @click="handleClick"> Message Box</jj-button>
          </template>

          <script>
            export default {
              methods: {
                handleClick() {
                    this.$msgbox(' alert ', {
                      alert: true,
                    })
                    .then(() => {
                      console.log('')
                    })
                    .catch(() => {
                      console.log('')
                    })
                }
              }
            }
          <\/script>
      `,

      vueCode2: `
          <template>
            <jj-button type="text" @click="handleClickSuccess"> type success  Message Box</jj-button>

            <jj-button type="text" @click="handleClickWarning"> type  warning  Message Box</jj-button>
          </template>

          <script>
            export default {
              methods: {
                  handleClickWarning() {
                    this.$msgbox(' warning ', {
                      type: 'warning',
                    })
                      .then(() => {
                        console.log('')
                      })
                      .catch(() => {
                        console.log('')
                      })
                  },

                  handleClickSuccess() {
                    this.$msgbox(' success ', {
                      type: 'success',
                    })
                      .then(() => {
                        console.log('')
                      })
                      .catch(() => {
                        console.log('')
                      })
                  },
              }
            }
          <\/script>
      `,
    }
  },
  methods: {
    handleClick() {
      this.$msgbox(' alert ', {
        alert: true,
      })
        .then(() => {
          console.log('')
        })
        .catch(() => {
          console.log('')
        })
    },

    handleClickWarning() {
      this.$msgbox(' warning ', {
        type: 'warning',
      })
        .then(() => {
          console.log('')
        })
        .catch(() => {
          console.log('')
        })
    },

    handleClickSuccess() {
      this.$msgbox(' success ', {
        type: 'success',
        // iconClass: 'jj-alipay',
        // showClose: false,
      })
        .then(() => {
          console.log('')
        })
        .catch(() => {
          console.log('')
        })
    },
  },
}
</script>

<style lang="scss" scoped></style>
