<template>
  <div class="content-wrapper">
    <h2>Drawer </h2>
    <p></p>

    <demo-block title="" :code-height="1350" introduction=", ">
      <template v-slot:code-des>
        <p>
           <code>visible</code>  <code>boolean</code>, true  DrawerDrawer <code
            >title</code
          >
           <code>body</code><code>title</code>  title  <code>slot</code>, 
          <code>title</code> , Drawer ,  <code>direction</code>,
           <code>direction</code> 
        </p>
      </template>
      <template v-slot:demo>
        <div class="demo-row">
          <jj-button type="primary" @click="showDrawer('Btt')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Rtl')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Ltr')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Ttb')"></jj-button>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{ $tools.formatCode(htmlCode) }}</code>
      </template>
    </demo-block>

    <demo-block title=" Title" :code-height="500" introduction=", ">
      <template v-slot:code-des>
        <p>
           title ,  <code>showTitle</code>  title , ,
          ,  <code>title</code> 
        </p>
      </template>
      <template v-slot:demo>
        <div class="demo-row">
          <jj-button type="primary" @click="isShowdrawer = true"></jj-button>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{ $tools.formatCode(htmlCode2) }}</code>
      </template>
    </demo-block>

    <jj-drawer title="" :visible.sync="drawerVisibleBtt" direction="btt">
      <template v-slot:title>
        <div></div>
      </template>
      <div></div>
    </jj-drawer>

    <jj-drawer title="" :visible.sync="drawerVisibleRtl" direction="rtl">
      <template v-slot:title>
        <div></div>
      </template>
      
    </jj-drawer>

    <jj-drawer title="" :visible.sync="drawerVisibleLtr" direction="ltr">
      <template v-slot:title>
        <div></div>
      </template>
      
    </jj-drawer>

    <jj-drawer title="" :visible.sync="drawerVisibleTtb" direction="ttb">
      <template v-slot:title>
        <div></div>
      </template>
      
    </jj-drawer>

    <jj-drawer :show-title="false" title="" :visible.sync="isShowdrawer">  </jj-drawer>

    <h3>Drawer Props</h3>
    <demo-table :data="propsDescription"></demo-table>
    <h3>Drawer Slot</h3>
    <demo-table :data="slotDescription"></demo-table>
    <h3>Drawer Events</h3>
    <demo-table :data="eventsDescription" type="events"></demo-table>
  </div>
</template>

<script>
export default {
  name: 'jjDrawerPage',
  data() {
    return {
      eventsDescription: [
        {
          parameter: 'open',
          description: 'Drawer ',
          cbAttr: '',
        },
        {
          parameter: 'opened',
          description: 'Drawer ',
          cbAttr: '',
        },
        {
          parameter: 'close',
          description: 'Drawer ',
          cbAttr: '',
        },
        {
          parameter: 'closed',
          description: 'Drawer ',
          cbAttr: '',
        },
      ],
      slotDescription: [
        {
          parameter: '',
          description: 'Drawer ',
          dataTypes: '',
          optional: '',
          default: '',
        },
        {
          parameter: 'title',
          description: 'Drawer ',
          dataTypes: '',
          optional: '',
          default: '',
        },
      ],
      propsDescription: [
        {
          parameter: 'append-to-body',
          description: 'Drawer  body ',
          dataTypes: 'Boolean',
          optional: '',
          default: 'false',
        },
        {
          parameter: 'direction',
          description: 'Drawer ',
          dataTypes: 'String',
          optional: 'rtl / ltr / ttb / btt',
          default: 'btt',
        },
        {
          parameter: 'size',
          description:
            "Drawer ,  number , ,  string ,  'x%',  number ",
          dataTypes: 'Number / String',
          optional: '',
          default: '30%',
        },
        {
          parameter: 'visible',
          description: ' Drawer .sync ',
          dataTypes: 'Boolean',
          optional: '',
          default: 'false',
        },
        {
          parameter: 'title',
          description: 'Drawer  slot ',
          dataTypes: 'String',
          optional: '',
          default: '',
        },
        {
          parameter: 'show-title',
          description: ' title ,  true,  false , title attribute  title slot ',
          dataTypes: 'Boolean',
          optional: '',
          default: 'true',
        },
      ],
      isShowdrawer: false,
      drawerVisibleRtl: false,
      drawerVisibleBtt: false,
      drawerVisibleLtr: false,
      drawerVisibleTtb: false,
      htmlCode: `
        <div class="demo-row">
          <jj-button type="primary" @click="showDrawer('Btt')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Rtl')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Ltr')"></jj-button>
          <jj-button type="primary" @click="showDrawer('Ttb')"></jj-button>
        </div>

        <jj-drawer title="" :visible.sync="drawerVisibleBtt" direction="btt">
          <template v-slot:title>
            <div></div>
          </template>
          
        </jj-drawer>

        <jj-drawer title="" :visible.sync="drawerVisibleRtl" direction="rtl">
          <template v-slot:title>
            <div></div>
          </template>
          
        </jj-drawer>

        <jj-drawer title="" :visible.sync="drawerVisibleLtr" direction="ltr">
          <template v-slot:title>
            <div></div>
          </template>
          
        </jj-drawer>

        <jj-drawer title="" :visible.sync="drawerVisibleTtb" direction="ttb">
          <template v-slot:title>
            <div></div>
          </template>
          
        </jj-drawer>

        <script>
          export default {
            data() {
              return {
                drawerVisibleRtl: false,
                drawerVisibleBtt: false,
                drawerVisibleLtr: false,
                drawerVisibleTtb: false,
              }
            },
            methods: {
              showDrawer(dir) {
                if (!this[\`drawerVisible\${dir}\`]) {
                  this[\`drawerVisible\${dir}\`] = true
                } 
              },
            },
          }
        <\/script>
      `,

      htmlCode2: `
        <div class="demo-row">
          <jj-button type="primary" @click="isShowdrawer = true"></jj-button>
        </div>

        <jj-drawer :show-title="false" title="" :visible.sync="isShowdrawer">  </jj-drawer>

        <script>
          export default {
            data() {
              return {
                isShowdrawer: false,
              }
            },
          }
        <\/script>
      `,
    }
  },
  methods: {
    showDrawer(dir) {
      if (!this[`drawerVisible${dir}`]) {
        this[`drawerVisible${dir}`] = true
      }
    },
  },
}
</script>

