import Vue from 'vue'
import modal from './modal.vue'

const ModalConstructor = Vue.extend(modal)

let instanceArr = []
/**
 *  this.$modal({ show: true, zIndex: this.zIndex - 1 })  this.$modal() 
 * @param {Object} options 
 * @returns
 */
const modalFunc = (options) => {
  // show
  if (options.show) {
    const instance = new ModalConstructor({
      data: options,
    }).$mount()
    instanceArr.push(instance)
    //  $mount()  elementOrSelector  (vue)  DOM API 
    document.body.appendChild(instance.$el)
    return instance
  } else {
    const instance = instanceArr.pop()
    // 
    if (instance && instance.$el && instance.$el.parentNode) {
      instance.$el.parentNode.removeChild(instance.$el)
    }
    return instance
  }
}

export default modalFunc
