import Vue from 'vue'
import msgBoxVue from './message-box.vue'
const MessageBoxConstructor = Vue.extend(msgBoxVue)

/**
 * @param {String} content 
 * @param {Object} options  MessageBoxConstructor 1options.data 2options  options.data 
 * @returns
 */
const MessageBox = (content = '', options) => {
  if (!options) {
    options = {
      content: content,
    }
  } else if (typeof options === 'object') {
    options = Object.assign(
      {
        content: content,
      },
      options
    )
  }

  const instance = new MessageBoxConstructor({
    data: options,
  }).$mount()

  document.body.appendChild(instance.$el)
  return instance.showConfirm()
}

export default MessageBox
